#wishlist .card {
    width: calc(66% - 40px);
    text-align: center;
    font-family: "Poppins", sans-serif;
}
#ticketForm {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: #edeef0;
    border-radius: 30px;
    padding-left: 20px;
    margin-bottom: 25px;
}
#content-input {
    flex: 1;
    border: none;
    outline: none;
    background: transparent;
    padding: 10px;
}
#addWish-button {
    border: none;
    outline: none;
    padding: 16px 50px;
    font-size: 16px;
    cursor: pointer;
    border-radius: 40px;
}

#TicketList ul {
    padding-inline-start: 20px;
}
#TicketList ul li {
    list-style: none;
    padding: 12px 12px 12px 0px;
}

#TicketList .deleteButton {
    outline: none;
    border: none;
    color: #555;
    text-align: center;
    border-radius: 10px;
    cursor: pointer;
}

.checked-content {
    text-decoration: line-through;
}

.ticket-checkbox {
    border-radius: 10px;
}

.ticket {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}
.left-row {
    display: flex;
}

.checked-content,
.unchecked-content {
    padding-left: 5px;
    padding-right: 5px;
}
